import React from 'react';

const SpringLombok = (props) => {
    return (
        <>
            <div className="common_style">
                <span className="lblock">
                    <small> <a href="https://projectlombok.org/features/all" target="_blank"> lombok  </a> </small>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@Getter / @Setter"> @Getter / @Setter </a> </span>
                        <span className="sblock">
                            <span className="sstitle"> Getter,Setter   </span>
                            <small>  public, @Setter(AccessLevel.[PRIVATE | PROTECTED ]) </small>
                            <span className="mblock">
                                <li className="col_r"> @Getter </li>
                                <li className="col_r"> @Setter </li>
                                <li> public class User {'{'}</li>
                                <li> &nbsp;private String id; </li>
                                <li> {'}'} </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@NoArgsConstructor"> @NoArgsConstructor #  </a> </span>
                        <span className="stitle"> <a name="@AllArgsConstructor"> @AllArgsConstructor #   </a>  </span>
                        <span className="stitle"> <a name="@RequiredArgsConstructor"> @RequiredArgsConstructor #   </a> </span>
                        <small> @NoArgsConstructor(access = AccessLevel.[ PRIVATE | PROTECTED ]) #3  , @AllArgsConstructor
                            , @RequiredArgsConstructor 2   () , @NoArgsConstructor @NonNull () </small>
                        <span className="sblock">
                            <span className="sstitle">    </span>
                            <span className="mblock">
                                <li className="col_r"> @NoArgsConstructor <small>  final   ,  @(force=true)  </small> </li>
                                <li className="col_r"> @AllArgsConstructor </li>
                                <li> public class User {'{'}</li>
                                <li> &nbsp;private String id; </li>
                                <li> {'}'} </li>
                                <li className="col_r"> @RequiredArgsConstructor <small>         ,    , @Builder
                                       , [final, @Nonnull]     </small> </li>
                                <li> public class User {'{'}</li>
                                <li> &nbsp;private final String id; #  </li>
                                <li> &nbsp;@Nonnull private String id; #  </li>
                                <li> &nbsp;private String id; #   </li>
                                <li> {'}'} </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@ToString"> @ToString </a> </span>
                        <span className="sblock">
                            <span className="sstitle"> ToString   </span>
                            <small> @ToString(exclude="password") #password  toString . </small>
                            <span className="mblock">
                                <li> @ToString </li>
                                <li> public class User {'{'}</li>
                                <li> &nbsp;private String id; </li>
                                <li> {'}'} </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@Data"> @Data </a> </span>
                        <span className="sblock">
                            <span className="sstitle"> @ToString + @EqualsAndHashCode + @Getter + @Setter
                                + @RequiredArgsConstructor </span>
                            <small>    , @AllArgsConstructor 
                                @RequiredArgsConstructor   </small>
                            <span className="mblock">
                                <li> @Data </li>
                                <li> public class User {'{'}</li>
                                <li> &nbsp;private String id; </li>
                                <li> {'}'} </li>
                            </span>
                        </span>
                    </span>
                    <span className="mblock">
                        <span className="stitle"> <a name="@Builder"> @Builder </a> </span>
                        <span className="sblock">
                            <small>         .
                                ,  default    () </small>
                            <span className="sstitle">   </span>
                            <span className="mblock">
                                <li> @Builder </li>
                                <li> public class User </li>
                                <li> &nbsp;private String id; </li>
                            </span>
                            <span className="sstitle">     </span>
                            <span className="mblock">
                                <li> public class User </li>
                                <li> &nbsp;private String id; </li>
                                <li> &nbsp;@Builder </li>
                                <li> &nbsp;public User(String id) {'{'} </li>
                                <li> &nbsp; this.id = id; </li>
                                <li> &nbsp;{'}'} </li>
                            </span>
                            <span className="sstitle"> build    </span>
                            <span className="mblock">
                                <li> {'public class User{'} </li>
                                <li> &nbsp;{'private String id;'} </li>
                                <li> &nbsp;{'public static class builder {'} </li>
                                <li> &nbsp;&nbsp;{'private String id;'} </li>
                                <li> &nbsp;&nbsp;{'public builder() { }'} </li>
                                <li> &nbsp;&nbsp;{'public builder id(String id) {'} </li>
                                <li> &nbsp;&nbsp;&nbsp;{'this.id = id;'} </li>
                                <li> &nbsp;&nbsp;&nbsp;{'return this;'} </li>
                                <li> &nbsp;&nbsp;{'    } '} </li>
                                <li> &nbsp;&nbsp;{'    public User build(){'} </li>
                                <li> &nbsp;&nbsp;&nbsp;{'      User user = new User(this.id);'} </li>
                                <li> &nbsp;&nbsp;&nbsp;{'      return user;'} </li>
                                <li> &nbsp;&nbsp;{'    }'} </li>
                                <li> &nbsp;{'    }'} </li>
                                <li> {'    }'} </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@EqualsAndHashCode"> @EqualsAndHashCode </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  equals(),hashCode()   </span>
                            <span className="mblock">
                                <li> (of={"1", "2"},callSuper=false) 
                                    1 2         . </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@Cleanup"> @Cleanup </a> </span>
                        <span className="sblock">
                            <span className="sstitle">   .close() .  </span>
                            <span className="mblock">
                                <li> @Cleanup ABC abc = new FileInput() # </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@Delegate"> @Delegate </a> </span>
                        <span className="sblock">
                            <span className="sstitle"> lombok??? </span>
                            <span className="mblock">
                                <li>      . </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@Syncronized"> @Syncronized </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@Value"> @Value </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@Slf4j"> @Slf4j </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@Log4j2"> @Log4j2 </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="@NonNull"> @NonNull </a> </span>
                        <span className="sblock">
                            <span className="sstitle"> NonNull #   . </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="">  </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                </span>
            </div>
        </>
    );
}

export default SpringLombok;