import React from 'react';

const SpringThymeleaf = (props) => {

    return (
        <>

            <div className="common_style">
                <span className="lblock">
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="">  </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li> thymeleaf  : https://www.thymeleaf.org/ </li>
                                <li> <a href="https://www.thymeleaf.org/doc/tutorials/3.0/usingthymeleaf.html#standard-expression-syntax" target="_blank">    thymeleaf   </a> </li>
                                <li>  : https://docs.spring.io/spring-boot/docs/2.3.1.RELEASE/reference/
                                    html/spring-boot-features.html#boot-features-spring-mvc-template-engines </li>
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="thymeleaf "> thymeleaf  </a> </span>
                        <span className="sblock">
                            <span className="sstitle"> thymeleaf   </span>
                            <span className="mblock">
                                <li> th:text #  </li>
                                <li> th:value="${'{}'}" #  </li>
                                <li> th:with="" </li>
                                <li> th:href="@{'{http://www.naver.com}'}"" </li>
                                <li> th:if="${'{}'} == ''{'}'}" #if  </li>
                                <li> th:unless="${'{}'} == ''{'}'}" #else  </li>
                                <li> th:attr="css" #html css   </li>
                                <li> th:each=" : ${'{}'}" </li>
                                <li> th: </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="thymeleaf "> thymeleaf  </a> </span>
                        <small> {' <html xmlns:th="http://www.thymeleaf.org"> '} #  </small>
                        <span className="sblock">
                            <small> {'Map<String,String> map = new HashMap<String, String>(); '} </small> <br />
                            <small> {' map.put("data1","data1");'} </small> <br />
                            <small> {' map.put("data2","data2");'} </small> <br />
                            <small> {'model.addAttribute("mapkey,map"); '} </small> <br />
                            <span className="sstitle">    , ${'{}'} </span>
                            <span className="mblock">
                                <li> {'<p th:text="${data1}">  </p>'} #data1    . </li>
                                <li> {'<p th:text="${mapkey.data1}">  </p>'} #data1    . </li>
                                <li> {'<p th:text="${mapkey.data2}">  </p>'} #data2    . </li>
                            </span>
                            {/*  */}
                            <span className="sstitle">    , ${'{}'} , *{'{}'} </span>
                            <span className="mblock">
                                <li> {' <tr th:object="${mapkey}"> '} </li>
                                <li> &nbsp;{' <td th:text="*{data1}"> '} </li>
                                <li> &nbsp;{' <td th:text="*{data2}"> '} </li>
                                <li> {' </tr> '} </li>
                            </span>
                            {/*  */}
                            <span className="sstitle">   </span>
                            <span className="mblock">
                                <li> {' <tr th:each=" : ${}"> '} </li>
                                <li> &nbsp;{'   <td th:text="${.}"> '} </li>
                                <li> &nbsp;{'   <td th:value="${.}"> '} </li>
                                <li> {' </tr> '} </li>
                            </span>
                            {/*  */}
                            <span className="sstitle">   ,  key  , #{'{}'}</span>
                            <span className="mblock">
                                <li> {' <p th:text="#{mapkey.DATA1}">  </p> '} </li>
                            </span>
                            {/*  */}
                            <span className="sstitle">   </span>
                            <span className="mblock">
                                <li> {' <a th:href="@{http://www.naver.com}"> naver </a> '} </li>
                            </span>
                            {/*  */}
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                            {/*  */}
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="">  </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                    <span className="mblock">
                        <span className="stitle"> <a name="">  </a> </span>
                        <span className="sblock">
                            <span className="sstitle">  </span>
                            <span className="mblock">
                                <li>  </li>
                            </span>
                        </span>
                    </span>
                    {/*  */}
                </span>
            </div>
        </>
    );
}

export default SpringThymeleaf;