import React from 'react';
import { Link } from 'react-router-dom';

const KeyMapList = () => {

    return (
        <div className="menucontainer">
            <div className="menu menu1">
                <Link to="/keymap_intellij">     </Link>
                <Link to="/keymap_vscode">     </Link>
                <Link to="/keymap_eclipse">     </Link>
            </div>
            <div className="menu menu2">
                <Link to="/keymap_window">     </Link>
                <Link to="/keymap_browser">     </Link>
            </div>
        </div>
    );
}

export default KeyMapList;