import React from 'react';
import { Link } from 'react-router-dom';

const SpringList = () => {


    return (
        <div className="menucontainer">
            <div className="menu menu1">
                <Link to="/spring_error"> <img src={process.env.PUBLIC_URL + '/img/NavMenu/Error_icon.svg'} />   </Link>
                <Link to="/spring_code">    </Link>
                <Link to="/spring_basic">   </Link>
                <Link to="/spring_thymeleaf"> <img src={process.env.PUBLIC_URL + '/img/NavMenu/Thymeleaf_icon.svg'} /> Thymeleaf </Link>
                <Link to="/spring_jpa">  JPA </Link>
                <Link to="/spring_lombok"> <img src={process.env.PUBLIC_URL + '/img/NavMenu/Lombok_icon.svg'} /> Lombok </Link>
                <Link to="/spring_annotation"> <img src={process.env.PUBLIC_URL + '/img/NavMenu/Annotation_icon.svg'} /> Annotation </Link>
                <Link to="/spring_setting"> <img src={process.env.PUBLIC_URL + '/img/NavMenu/Settings_icon.svg'} />    </Link>
                <Link to="/spring_api">   API </Link>
                <Link to="/spring_oauth2">   OAuth2  </Link>
                <Link to="/spring_security">  Security </Link>

            </div>
            <div className="menu menu2">
                <Link to="/spring_securityexcode1">    </Link>
                <Link to="/spring_securityexcode2"> MySQL - Login </Link>
                <Link to="/spring_securityexcode3"> MySQL - Login - Security </Link>
                <Link to="/spring_securityexcode4">  Security ExCode4 </Link>
            </div>
        </div>
    );
}

export default SpringList;